@font-face {
    font-family: "font-1";
    src: url(../fonts/Roboto-Regular.ttf);
}
@font-face {
    font-family: "font-2";
    src: url(../fonts/Roboto-Light.ttf);
}
@font-face {
    font-family: "font-3";
    src: url(../fonts/Roboto-Bold.ttf);
}

$font-1: "font-1", sans-serif;
$font-2: "font-2", sans-serif;
$font-3: "font-3", sans-serif;

$color-1: #ff3d00;
$color-2: rgba(255, 61, 0, 0.5);
$color-3: #546fe4;
$color-4: #aaf0d1;
$black: #04081d;
$white: rgb(253, 245, 245);
  
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: $font-1;
}
body {
    background: $black;
    color: $white;
    transition: 2s;
    user-select: none;
    overflow-x: hidden,
}

h2 {
    text-align: center;
}
li {
    list-style: none;
}

@mixin flex-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
@mixin center-child {
    display: flex;
    justify-content: center;
    align-items: center;
}
.d-none {
    display: none;
}