.project-page {
    @include center-child;
    height: 100vh;
    width: 100vh;
    margin: auto;
  }
  
  .project-container {
    display: flex;
    justify-content: center;
    gap: 10px;
    max-width: 1400px;
  }
  
  .project-card {
    background-color: rgba($white, 0.7);
    border-radius: 10px;
    text-align: center;
    color: $black;
    height: 800px;
    transition: 0.5s;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
  
    &:hover {
      width: 800px;
      transition: 1.5s;
      img {
        opacity: 1;
        transition: 1s;
        transition-delay: .5s;
        z-index: 10;
      }
      h2 {
        transform: rotate(0) translatey(-150px);
        transition: 1s;
      }
      .sticker {
        width: 95%;
        height: 95%;
        top: 2%;
        transition: 1s;
      }
      .circle {
        opacity: 0;
        transition: .5s;
      }
    }
  
    h2 {
      font-size: 36px;
      transform: translateY(-100px) rotate(-55deg);
      z-index: 10;
    }
  
    img {
      width: 80%;
      height: auto;
      object-fit: cover;
      border-radius: 10px;
      opacity: 0;
    }
  }
  
  @media screen and (max-width: 768px) {
    .project-card {
      width: 100%;
    }
  }
  
  @media screen and (max-width: 400px) {
    .project-card {
      height: 400px;
    }
  }
  
.sticker {
    position: absolute;
    top: 10%;
    height: 300px;
    width: 60%;
    border-radius: 5px;
    border: 1px solid $black;
    background-color: rgba($white,0.8);
    transition: 1s;
}

  .circle {
    position: absolute;
    top: 70%;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    border: 1px solid $black;
    background-color: rgba(red,0.8);
    transition: .5s;
  }