.change-page {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  @media screen and (max-width: 768px) {
    display: none;
  }
 .arrow {
    position: absolute;
    text-decoration: none;
    visibility:visible;
    color: $color-1;
    &:hover > span {
      font-size: 8rem;
      transition: .5s;
    }
    span {
      font-size: 10rem;
      font-weight: 800;
      transition: .5s;
    }
    p {
      text-align: center;
      font-size: 2rem;
    }
  } 
  .left {
    left: 10px;
    transform: translateY(-50%);
  }
  .right {
    right: 10px;
    transform: translateY(-50%);
  }
  .up {
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
  }
  .down {
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
  }
}