h1 {
    margin: 500px auto 50px;
    span {
        font-size: 5rem;
    }
    @media screen and (max-width: 768px) {
        margin: 250px auto 50px;
        .letter, .letter-bis {
            font-size: 3rem;
        }
    }
}
h2 {
    font-size: 2.5rem;
    @media screen and (max-width: 768px) {
        font-size: 1.5rem;
    }
}
.blob-photo-container {
    position: relative;
    display: flex;
    height: 600px;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
    .blob-photo {
        user-select: none;
        position:absolute;
        display: block;
        width: 600px;
        height: 600px;
        border-radius: 50%;
        box-shadow: 0 0 20px $color-2;
        margin-bottom: 200px;
    }
    .blob-photo img {
        border-radius: 50%;
        object-fit:cover;
        height: 100%;
        width: 100%;
    }
}