.project {
    height: 100vh;
    width: 95%;
    margin: auto;
    @include center-child;
    .project-main {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5%;
        background-color: $color-1;
        border-radius: 500px;
        height: 50vh;
        padding: 100px 10px;
        @media screen and (max-width: 768px){
          flex-direction: column;
          justify-content: center;
          padding: 0;
          height: 1400px;
        }
        .project-content {
            width: 30%;
            color: $black;
            @include center-child;
            flex-direction: column;
            @media screen and (max-width: 768px){
              width: 90%;
              text-align: center;
            }
            h2 {
                font-size: 65px;
                position: absolute;
                top: 50px;
                @media screen and (max-width: 768px){
                  position: static;
                  margin-top: 50px;
                }
            }
            .project-without-title {
              margin-top: 100px;
            }
            .languages-ul {
              display: flex;
              justify-content: space-evenly;
              .languages-li {
                font-size: 20px;
                padding: 10px;
                border-radius: 50%;
              }
            }
            p {
                font-size: 25px;
                margin: 50px 0;
                text-align: center;
            }
            .button-container {
              a {
                padding: 20px;
                border-radius: 50%;
                font-size: 20px;
                text-decoration: none;
                color: $black;
              }
            }
        }
    }
}

.line {
  border-top: 1px solid black;
  width: 90%;
  margin: 30px auto;
}

.img-content {
  width: 30%;
  @media screen and (max-width: 940px){
    display: flex;
    width: 80%;
    margin-top: 50px;
  }
}

.img-container {
    border-radius: 5px;
    box-shadow: 0px 2px 4px rgba(51, 51, 51, 0.26);
    width: 100%;
    height: auto;
    overflow: hidden;
    position: relative;

    @media screen and (max-width: 940px){
      height: auto;
      width: 100%;
      align-self: center;
    }
    @media screen and (max-width: 540px){
      height: 200px;
      width: 370px;
    }
    @media screen and (max-width: 400px){
      height: 150px;
      width: 280px
    }
    img {
        overflow: hidden;
        object-fit: cover;
        object-position: 0%;
        width: 100%;
        height: 100%;
        position: absolute;
        transition: 0.4s ease-out;
        position: relative;
        cursor: pointer;
        &:hover {
          filter: opacity(85%) contrast(120%);
        }
    }
}

.ReactModal__Overlay {
  z-index: 10;
  height: 100vh;
  width: auto;
}
.modal-content {
  img {
    height: 100%;
    width: 100%;
  }
  .close-button {
    position: absolute;
    right: 0;
    height: 100px;
    width: 150px;
    font-size: 40px;
    background-color: $white;
    border-radius: 50%;
    cursor: pointer;
  }
}