.smart-nav {
    position: relative;
    height: 1px;
    margin-left: 20px;
    img {
        position: absolute;
        height: 150px;
        transition: 1s;
        &:nth-child(1) {
            top: 0;
        }
        &:nth-child(2) {
            top: 40px;
        }
        &:nth-child(3) {
            top: 80px;
        }       
    }
}
.rotate-down {
    transform: rotate(45deg) translateY(50px);
    transition: 1s;
}
.hide {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.6s;
}
.rotate-up {
    transform: rotate(-45deg) translateY(-50px);
    transition: 1s;
}

.open-nav {
    position: fixed;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $black;
    width: 80%;
    text-align: center;
    border: 1px solid $color-1;
    border-radius: 10px;
    top: 400px;
    opacity: 1;
    transition: opacity 0.6s;
    z-index: 10;
    a {
      text-decoration: none;
      color: $white;
    }
    li {
      font-size: 3rem;
      line-height: 150px;
      &:hover {
        background-color: $color-1;
        color: $black;
      }
    }
}