.form-container {
  @include center-child;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}
form{
    display: grid;
    width: 50%;
    min-width: 300px;
    max-width: 600px;
    margin: 0 auto;
    margin-top: 50px;
    gap: 10px;
    background-color: $black;
    border-radius: 50px;
    label {
      font-size: 2rem;
      text-align: center;
    }
    input {
      height: 50px;
      font-size: 2rem;
      border-radius: 15px;
      border: none;
      padding: 0 10px;
    }
    textarea {
      height: 250px;
      resize: none;
      font-size: 1.6rem;
      border-radius: 15px;
      border: none;
      padding: 0 10px;
    }
    .btn-submit {
      cursor: pointer;
      border-radius: 5px;
      margin-bottom: 50px;
      transition: 1s;
      background-color: $color-1;
      width: 60%;
      margin: 10px auto;
      &:hover {
        border-radius: 20px;
        transition: 1s;
        letter-spacing: 5px;
        background-color: $color-2;
      }
    }
    @media (max-width: 1000px) {
      width: 98%;
    }
  }
  .about {
    background-color: $color-1;
    border-radius: 50px;
    margin-top: 200px;
    height: 100px;
    width: 350px;
    @include center-child;
    p {
      font-size: 2rem;
    }
  }

  .form-message {
    position: absolute;
    top: 400px;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .success, .error {
    width: 400px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: 50px;
    border-radius: 5px;
  }
  .success {
    background-color: green;
  }
  .error {
    background-color: red;
  }