nav {
    position: absolute;
    top: 2rem;
    left: 1.4rem;
    font-size: 2.4rem;
    line-height: 100px;
    color: $black;
    z-index: 1;
    a {
      text-decoration: none;
      color: $black;
    }
    li {
      transition: .3s;
      transition-delay: 0.2s;
    }
    .nav-background {
      position: absolute;
      height: 500px;
      width: 500px;
      border-radius: 50%;
      background: $color-1;
      top: -150px;
      left: -160px;
      z-index: -1;
      animation: navAnim 20s infinite alternate;
      @keyframes navAnim {
        50% {
          transform: scale(1.2);
        }
        100% {
          transform: scale(1);
        }
      }
    }    
    .nav-active {
      color: $black;
    }
    ul:hover li:not(:hover) {
      opacity: .5;
    }
    ul:hover li:hover {
      color: $black;
      background-color: $white;
      border-radius: 50%;
      transition: .5s;
      transform: translateX(50px);
    }
  
    .nav-portfolio {
      cursor: pointer;
      display: flex;
      transition: .5s;
      li {
        transform: rotatey(90deg);
      }
  
      &:hover .nav-projects,
      .nav-projects:hover {
  
        visibility: visible;
        opacity: 1;
  
        li {
          transform: rotatey(0deg);
          transition: 1s;
          &:hover {
            width: auto;
            transition-delay: 0.1s;
          }
        }
      }
      .nav-projects {
        position: absolute;
        visibility: hidden;
        opacity: 0;
        padding: 0 20px;
        width: 500px;
        transform: translateX(100px);
        transition: .3s ;
        background-color: $color-1;
        border-radius: 50%;
        padding: 0 190px;
        li {
          transition: .3s;
          font-size: 2rem;
          color: $black;
          text-decoration: none;
        }
      }
    }
  }