.dynamic-text {
  position: relative;
  display: flex;
  justify-content: center;

  #firstName-target,
  #lastName-target {
    display: flex;
    width: 280px;
    text-align: left;
    @media screen and (max-width: 768px) {
      width: 165px;
      }
  }
  #lastName-target {
    justify-content: flex-end;
  }

  @keyframes anim {
    0% {
      opacity: 0;
      transform: translateX(200px);
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
  @keyframes animBis {
    0% {
      opacity: 0;
      transform: translateX(-200px);
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
}